<template>
    <div class="box3">
        <p class="p1">交易对币种行情</p>
        <div class="nav">
            <div @click="Click(item)" :class="item.selected==true?'active':''" class="li" v-for="item in nav" :key="item.index">
               <p>{{item.name}}</p> 
            </div>
        </div>
        <v-coin ref="coin" :data="channel"></v-coin>

    </div>
</template>
<script>
import coin from './coin'
import http from '../../http/index'
import { createSocket, sendWSPush } from '../../utils/socket'
export default {
    components:{
        "v-coin": coin,
        
    },
    data(){
        return{
            nav:[{
                name:'QCC区',selected:true,channel:'top_all_qc'
            },
            {
                name:'USDT区',selected:false,channel:'top_all_usdt'
            },
            {
                name:'BTC区',selected:false,channel:'top_all_btc'
            },
            {
                name:'PAX区',selected:false,channel:'top_all_pax'
            },
            {
                name:'LTC区',selected:false,channel:'top_all_ltc'
            },],
            channel:'top_all_qc'
        }
    },
    methods:{
        Click(item){
            var params = {
                event: "removeChannel",
                channel: this.channel
            }
            sendWSPush(params)
            for(var i in this.nav){
                this.nav[i].selected=false
            }
            item.selected=true
            this.channel=item.channel
            this.$refs.coin.channel=item.channel
            this.$refs.coin.getTrend(item.channel)
        },

    
    },
    mounted(){
            this.$refs.coin.channel='top_all_qc'
            this.$refs.coin.getTrend('top_all_qc')
    }
}
</script>
<style lang="stylus">
.box3
    width 64% 
    margin 0 auto 
    padding-top .5rem
    padding-bottom 1.3rem
    .p1 
        color #fff 
        font-size .3rem
        margin-bottom .2rem
    .p2 
        color #A3AED2
        font-size .16rem
    .p3 
        color #fff 
    .nav 
        width 100% 
        height 46px 
        background #1C1D31
        border-radius 23px
        display flex 
        justify-content space-between
        .li 
            display flex
            justify-content center
            width 100%
            align-items center
            font-size 14px
            color #A5C2FF
        .active
            background #588BF7
            color #ffffff
            border-radius 23px
</style>


