<script>
import http from '../../http/index'
import { createSocket, sendWSPush } from '../../utils/socket'
export default {
    data(){
        return{
            channel:'markets',
            data:[{name:'BTC',riseRate:'',img:require('../../../static/image/index_bz_pic_01.png'),lastPrice:'',CNY:''},
                    {name:'ETH',riseRate:'',img:require('../../../static/image/index_bz_pic_02.png'),lastPrice:'',CNY:''},
                    {name:'LTC',riseRate:'',img:require('../../../static/image/index_bz_pic_03.png'),lastPrice:'',CNY:''},
                    {name:'EOS',riseRate:'',img:require('../../../static/image/index_bz_pic_04.png'),lastPrice:'',CNY:''},]
        }
    },
    beforeDestroy() {
      this.over();
    },
    methods:{
        getBtc(){
            var self =this
 
            const getDataFunc = function(e) {
                var data=JSON.parse(e.detail.data.data.replace("(","").replace(")",""))
                console.log(data)
                if(data.channel=='top_all_usdt'){
                    self.data[0].riseRate=data.datas[1].riseRate
                    self.data[0].lastPrice=data.datas[1].lastPrice
                    self.data[1].riseRate=data.datas[2].riseRate
                    self.data[1].lastPrice=data.datas[2].lastPrice
                    self.data[2].riseRate=data.datas[6].riseRate
                    self.data[2].lastPrice=data.datas[6].lastPrice
                    self.data[3].riseRate=data.datas[4].riseRate
                    self.data[3].lastPrice=data.datas[4].lastPrice
                }else if(data.channel=='usdtqc_ticker'){
                    self.data[0].CNY=(self.data[0].lastPrice*data.ticker.last).toFixed(4)
                    self.data[1].CNY=(self.data[1].lastPrice*data.ticker.last).toFixed(4)
                    self.data[2].CNY=(self.data[2].lastPrice*data.ticker.last).toFixed(4)
                    self.data[3].CNY=(self.data[3].lastPrice*data.ticker.last).toFixed(4)
                }
            } 
            window.addEventListener('onmessageWS', getDataFunc)
        }
    },
    mounted(){

   

    },
    created(){
        this.getBtc()
    }
}
</script>
<style lang="stylus">
