                width 80%
                padding 0  10%
                position absolute
                display flex 
                top .3rem
                justify-content space-between
                .left
                    display flex 
                    width 50%
                    img 
                        width 20px 
                        height 20px 
                        margin-right .11rem
            .bottom
                z-index 20
                position absolute
                bottom .4rem
                width 80%
                margin 0 auto
                left 10%
                font-size .16rem
      

    

</style>


