<template>
  <div class="home">
    <v-header :className="className"></v-header>
    <v-carousel :className="className"></v-carousel>
    <v-hot :className="className"></v-hot>
    <v-market :className="className"></v-market>
    <v-introduct></v-introduct>
    <v-join></v-join>
    <v-bottom></v-bottom>
  </div>
</template>
<script>
import carousel from "./carousel";
import header from "./header";
import hot from './hot'
import market from './market'
import introduct from './introduct'
import join from './join'
import bottom from './bottom'
import { createSocket, sendWSPush } from '../../utils/socket'
export default {
  components: {
    "v-header":header,
    "v-carousel": carousel,
    "v-hot" :hot,
    "v-market" :market,
    "v-introduct":introduct,
    "v-join":join,
    "v-bottom":bottom

  },
  data() {
    return {
        className:''
    };
  },
      created: function () {
        if(document.documentElement.clientWidth > 640){
            this.className = 'pc';

        }else{
            this.className = 'mobile';
        }
               this.getWS()
    },
    methods:{
          getWS(){
            var self =this
           var params = {
                event: "addChannel",
                channel: 'top_all_usdt'
            }            
            var params1 = {
                event: "addChannel",
                channel: 'usdtqc_ticker'
            }
           
 

            createSocket() //创建
            // sendWSPush(params) //发送数据
            // sendWSPush(params1) 
    
 

        }
    }
};
</script>
<style lang="stylus">
.home 
    background #131324

</style>


