<template>
    <div class="box4">
        <div class="box4_header">
            <div class="li" v-for="item in this.title" :key="item.index">
                <p>{{item.name}}</p>
            </div>
        </div>
        <div class="content">
            <div v-for="item in coinData" :key="item.index" class="content_li">
                <p>{{item.market}}</p>
                <p>{{item.lastPrice}}</p>
                <p >{{item.hightPrice}}</p>
                <p>{{item.lowPrice}}</p>
                <p :style="item.riseRate>0?'color:#EE6560':'color:#4DB872'">{{item.riseRate}}</p>
                <p>{{item.vol}} {{item.showName}}</p>
            </div>
        </div>
        
    </div>
</template>
<script>
import { createSocket, sendWSPush } from '../../utils/socket'
export default {

    data(){
        return{
            title:[{name:'交易对',},
            {name:'实时价格',},
            {name:'24H最高价',},
            {name:'24H最低价',},
            {name:'24H涨幅',},
            {name:'成交量',}],
            coinData:[],
            channer:'',

        }
    },
    methods:{
       getTrend(channel){
           var self =this
            var params = {
                event: "addChannel",
                channel: channel
            }
            sendWSPush(params)
        const getDataFunc = function(e) {
                var data=JSON.parse(e.detail.data.data.replace("(","").replace(")",""))
                console.log(data)
                if(data.channel==channel){
                    self.coinData =data.datas
                }
            
            } 
            window.addEventListener('onmessageWS', getDataFunc)
       }
    },
    mounted(){
       console.log(this.data)
    },

}
</script>
<style lang="stylus">
.box4
    width 100%
    margin 0 auto 
    .box4_header 
        width 100% 
        height 50px 
        border-bottom 1px solid #1C1D31
        border-radius 23px
        display flex 
        justify-content space-between
        .li 
            display flex
            justify-content center
            width 100%
            align-items center
            font-size 14px
            color #A2A7C3
    
    .content::-webkit-scrollbar 
        width:0px;
        height:0px;
    .content 
        width 100%
        height 6rem
        overflow-y scroll
        .content_li
            border-bottom 1px solid #1C1D31
            border-radius 23px
            display flex 
            justify-content space-between
            color #fff
            p 
                width 100%
                text-align center
                padding .1rem 0
                font-size .14rem


</style>


