<template>
  <div class="box1" :class="className">
    <div class="bg"></div>
    <div class="main">

        <div class="left">
        <div class="logo">
            <img src="../../../static/image/logo.png" alt />
        </div>
        <div class="nav">
            <div class="li" v-for="item in nav" :key="item.index">
            <p>{{item.name}}</p>
            </div>
        </div>
        </div>
        <div class="right">
            <div>
            登录
            </div>
            <div class="active">
            注册
            </div>
        </div>
    </div>
  </div>
</template>

<script>
export default {
    props:['className'],
  data() {
    return {
      
      nav: [
        { name: "行情中心" },
        { name: "法币交易" },
        { name: "合约交易" },
        { name: "币币交易" }
      ]
    };
  },

  mounted() {
      
  }
};
</script>
<style lang="stylus">
.box1 
    width: 100%;
    opacity 0.8
    position absolute 
    top 0
    z-index 99
    height 0.5888rem
    background: #131324;
    .bg 
        height 0.5888rem
        background: #131324;
        position absolute 
         top 0
    .main 
        height 0.5888rem
        display flex
        position absolute 
        top 0
        width 100%
        align-items center
        justify-content space-between
        .left 
            display flex
            padding .17rem 0
            margin-left 1.024rem
            align-items center
            .nav 
                display flex 
                color #A3AED2
                font-size .0.1792rem
                .li 
                    margin-right .4rem
            img 
                width 1.4848rem 
                height 0.384rem
                
                margin-right .5rem
        .right 
            color #fff
            display flex
            margin-right .8rem
            div
                height 25px 
                line-height 25px
            .active
                background #588BF7
                width .6rem 
                height 25px
                font-size .14rem
                border-radius .13rem
                text-align center 
                line-height 25px
                margin-left .1rem
.box1 >>>.swiper-pagination-bullet
  width 50px!important 
  background #fff!important


 

</style>



