<template>
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <swiper :options="swiperOption" ref="mySwiper">
        <swiper-slide
          :key="index"
          style="width:100%;height:6.8rem"
          v-for="(item,index) in swiperData"
        >
          <img :src="item.image" alt />
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
  </div>
</template>

<script>
import http from "../../http/index";
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  data() {
    return {
      swiperData: [
        { image: require("../../../static/image/banner1.png") },
        { image: require("../../../static/image/banner2.png") },
        { image: require("../../../static/image/banner3.png") }
      ],
      swiperOption: {
        // 所有配置均为可选（同Swiper配置）
        initialSlide: 0,
        pagination: ".swiper-pagination",
        pagination: {
          el: ".swiper-pagination"
        },
        loop: false,
        allowTouchMove: true,
        direction: "horizontal",
        paginationClickable: true,
        mousewheelControl: false,
        autoplayDisableOnInteraction: false,
        observer: true,
        observeParents: true,
        autoplay: true
      }
    };
  },
  components: {
    swiper,
    swiperSlide
  },
  mounted() {}
};
</script>
<style lang="stylus">
.swiper-container 
  width 100%
  height 6.8rem
  .swiper-wrapper 
    width 100%
    height 100%
    img 
      width 100%
      height 100%
</style>



