<template>
    <div class="box6">
        <div class="content">
            <p class="p1">全平台终端接入</p>
            <p class="p2">iOS、Android、PC、H5 多个平台支持全业务功能</p>
            <div class="ul">
                <div class="li" v-for="item in data" :key="item.index">
                    <img :src="item.img" alt="">
                    <p class="p3">{{item.name}}</p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

export default {

    data(){
        return{ 
            data:[{img:require('../../../static/image/index_xz_ico_01.png'),name:'IOS版下载'},
            {img:require('../../../static/image/index_xz_ico_02.png'),name:'Android版下载'},
            {img:require('../../../static/image/index_xz_ico_03.png'),name:'PC版地址'},
            {img:require('../../../static/image/index_xz_ico_04.png'),name:'H5版地址'},]
        }
    },
    methods:{
    

    
    },
    mounted(){
        
    }
}
</script>
<style lang="stylus">
.box6
    width 100%
    margin 0 auto 
    height 2.92rem
    background #1C1D31
    padding-top .6rem 
    .content 
        width 52.5%
        margin 0 auto 

        text-align center
        .p1 
            font-size .3rem 
            color #fff
            margin 0 0 .2rem 0
        .p2 
            font-size .16rem 
            color #A3AED2
        .ul 
            margin .55rem  0
            display flex 
            justify-content space-between
            .li 
                width 1.5rem 
                height .7rem 
                padding 0 .25rem
                border 2px solid #606779
                border-radius .06rem
                display flex 
                justify-content space-around
                align-items center
                img     
                    width .32rem
                    height .32rem
                .p3 
                    font-size .16rem 
                    color #A3AED2

</style>


